import React, { ReactElement, useEffect, useState } from 'react'
import Popup from 'reactjs-popup'
import classNames from 'classnames';
import './halfGOptimalProportion.css'
import { useHistory } from 'react-router-dom';



export default function HalfGOptimalProportion(): ReactElement {
  const [percentState, setPercentState] = useState(false)


  const history = useHistory();


  const handleSubmit = () => {
    if (percentState === false) {
      localStorage.setItem('halfGuaranteePercent', '60%')
    } else if (percentState === true) {
      localStorage.setItem('halfGuaranteePercent', '40%')
    }

    history.push("/percent-calculation");
  };

  const handlePercent60 = () => {
    setPercentState(false)
  }

  const handlePercent40 = () => {
    setPercentState(true)
  }

  const handleRotate = () => {
    setPercentState((prev) => !prev)

  }

  const rotateClass = classNames('switch_button', {
    'rotate40': percentState,
    'rotate60': !percentState,
  })

  useEffect(() => {
    console.log(percentState);

  }, [percentState])

  return (
    <div className="half-optimal-proportion">
      <div className="half-optimal-proportion_text">
        Pour la solution mi-garantie/mi-rendement, vous définissez une partie garantie (100% sécurisée, mais avec un taux de 0.1%) et une partie en fonds (non garantie mais avec  un rendement plus intéressant).
      </div>

      <div className="half-optimal-proportion_percents">
        <div className="proportion_percent">
          <div onClick={handlePercent60} className="percent gold_text">
            60%
          </div>
          <div className="percent-desc">
            Capital garanti
          </div>
        </div>

        <div onClick={handleRotate} className={rotateClass}>
          <img className="switch_img" src="/image/switcher.svg" alt="switcher" />
        </div>

        <div className="proportion_percent">
          <div onClick={handlePercent40} className="percent gold_text">
            40%
          </div>
          <div className="percent-desc">
            Fonds de placement
          </div>
        </div>
      </div>

      <button onClick={handleSubmit} className="next_button button_position">
        Continuer
      </button>

      <Popup
        modal
        trigger={
          <div className="pop_up_triger">
            Quelle est la proportion optimale ?
          </div>
        }
      >
        {(
          close:
            | ((event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void)
            | undefined
        ) => (
          <>
            <button className="close" onClick={close}>
              X
            </button>
            <div className="pop_up">
              <div className="pop_up_title">
                Quelle est la proportion optimale ?
              </div>
              <div className="pop_up_text">
                Il n’existe pas de choix optimal par défaut. La répartition dépend fortement de votre propension au risque.
              </div>
              <ul className="pop_up_list">
                <li className="pop_up_list-item">La partie « capital garanti » est identique à la solution garantie, une épargne classique avec un taux d’intérêt très faible. </li>
                <li className="pop_up_list-item">La partie fonds de placement est plus risquée, mais peut avoir un retour sur investissement beaucoup plus élevé. </li>
              </ul>
              <div className="pop_up_text">
                Cette solution est la solution recommandée pour la plupart des profils d'investissement, car elle permet un rendement plus intéressant que le capital garanti sans exposer à un risque trop élevé.
                Le taux de 0.20% sur la partie garantie est indicatif et dépend du type produit, de la compagnie d'assurance ou de la banque et des conditions de marché.
              </div>

            </div>
          </>
        )}
      </Popup>
    </div>
  )
}
